
import { Component, ElementRef, ViewChild } from '@angular/core';
import { HttpClient } from "@angular/common/http";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  public img: any;
  public itemImg: any;
  public name: string;
  public phone: string;
  public address: string;
  public timing: string;
  public itemName: string;
  public itemPrice: number;
  public shopId: number;
  public shopArray: any[];

  constructor(private http: HttpClient) { 
    this.getAllShops()
  }
  @ViewChild('file') file: ElementRef;
  @ViewChild('itemImage') itemImage: ElementRef;
  title = 'My Application';
  form = new FormData();
  itemForm = new FormData();

  validate(v) {
    return (v.length === 12)
  }

  change(e) {
    this.img = e.target.files[0]
  }

  changeItem(e) {
    this.itemImg = e.target.files[0]
  }

  imageUpload(img) {
    let imageForm = new FormData();
    this.form.set('image', img);
    this.form.set('key', '28c9b266952ca9d1a09972ed08c96fb9');
    return new Promise<void>((resolve, reject) => {
      fetch('https://api.imgbb.com/1/upload', { method: 'POST', body: imageForm })
        .then(response => response.json())
        .then(json => resolve(json.data.url))
        .catch(reject)
    })
  }

  submitShop() {
    this.imageUpload(this.img).then(url => {
      let data = {
        name: this.name,
        address: this.address,
        image: url,
        timing: this.timing,
        phone: this.phone
      }
      this.http.post('./v1/api/store', data).subscribe(
        () => {
          this.img = null;
          this.file.nativeElement.value = null;
          this.name = null;
          this.phone = null;
          this.address = null;
          this.timing = null;
          alert('Shop added')
        },
        () => {}
      )
    })
  }

  submitItem() {
    this.imageUpload(this.itemImage).then(url => {
      let data = {
        name: this.itemName,
        price: this.itemPrice,
        image: url,
        shopId: this.shopId
      }
    })
  }

  getAllShops() {
    fetch('./v1/api/store').then(d=>d.json()).then(json=>{this.shopArray = json})
  }

}
